.money-list {
    width: fit-content;
    border: 2px solid #fff;
    background-color: #222;
    color: #fff;
}

.number-money {
    padding: 4px 10px;
}

.number-money:nth-child(5),
.number-money:nth-child(10),
.number-money:nth-child(15) {
    background-color: #444;
}

.number-money .number {
    margin-right: 20px;
}

.number-money.selected {
    background-color: #002fff;
}